import React from "react"

const getProjectLogo = projectName =>
  `https://raw.githubusercontent.com/GanapathyPT/${projectName}/master/public/logo.png`

export default function ProjectCard({ project }) {
  return (
    <div className="card" key={project.id}>
      <h2>{project.name}</h2>
      <img src={getProjectLogo(project.name)} alt={project.description} />
      <div className="content">
        <h4 align="center">{project.name}</h4>
        <p>{project.description}</p>
        <div className="actions">
          <a rel="noreferrer" target="_blank" href={project.homepageUrl}>
            Visit
          </a>
          <a rel="noreferrer" target="_blank" href={project.url}>
            Source
          </a>
        </div>
      </div>
    </div>
  )
}
