import { Link } from "gatsby"
import React, { useEffect, useState } from "react"
import Typist from "react-typist"

import Layout from "../components/Layout"
import ProjectCard from "../components/ProjectCard"
import { useProjects } from "../hooks/useProjects"

const copy = arr => JSON.parse(JSON.stringify(arr))
const getFour = arr => {
  const arrCopy = copy(arr)
  return arrCopy.splice(0, 4)
}

export default function Projects() {
  const { pinnedProjects, allProjects } = useProjects()
  const [projects, setProjects] = useState(pinnedProjects)
  const [loadMoreVisible, setLoadMoreVisible] = useState(true)

  useEffect(() => {
    setProjects(copy(pinnedProjects))
  }, [pinnedProjects])

  const loadMore = () => {
    setProjects(oldState => {
      const newProjects = copy(oldState)
      allProjects
        .filter(project => !!project.homepageUrl && !!project.description)
        .filter(project => !project.name?.includes(".github.io"))
        .forEach(project => {
          if (
            !newProjects.some(
              featuredProject => project.id !== featuredProject.id
            )
          )
            newProjects.push(project)
        })
      return newProjects
    })
    setLoadMoreVisible(false)
  }

  return (
    <Layout>
      <div>
        <Typist
          cursor={{
            show: false,
          }}
        >
          <Typist.Delay ms={100} />
          <h1 className="projects-header">Featured Projects</h1>
        </Typist>
        <Link to="/all-projects" className="project-view-all-btn">
          View all
          <i className="fa fa-arrow-right"></i>
        </Link>
      </div>
      <div className="projects-container fade-in">
        {(loadMoreVisible ? getFour(projects) : projects).map(project => (
          <ProjectCard project={project} key={project.id} />
        ))}
        {loadMoreVisible && (
          <div className="projects-load-more" onClick={loadMore}>
            load more
            <i class="fa fa-spinner"></i>
          </div>
        )}
      </div>
    </Layout>
  )
}
